h type of construction and furnishing material is tasteful and follows the latest trends, fitting into every corner of the world and lightning up people’s lives."),_c('br'),_c('br'),_vm._v("Facing the dramatic changes in the global market, Sureseen has actively observed market demands and taken on challenges in the dynamic market. The company has been vigorous in the development and innovation of substitute products and has continuously contributed to the wooden construction material industry at home in Taiwan and around the world. In the last 50 years, Sureseen has transformed from a small-scale construction material company to an importer of wooden construction material and has won the trust and support of customers in the construction, furnishing, packaging, and furniture industries. We have striven to put into practice the principle of “establishing the benchmark, giving back with joy” and continue with globalized and detail-oriented development as we maintain the leading position in Taiwan’s wooden construction material industry.")]):_vm._e()]),_c('div',{staticClass:"right-side"},[(_vm.language.chinese)?_c('section',{staticClass:"features"},[_vm._m(0),_vm._m(1),_vm._m(2),_vm._m(3)]):(_vm.language.english)?_c('section',{staticClass:"features"},[_vm._m(4),_vm._m(5),_vm._m(6),_vm._m(7)]):_vm._e()])])}
